*,
::after,
::before {
  box-sizing: border-box;
  /* border: 1px solid red; */
}
/* fonts */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&family=Montserrat&display=swap');

html {
  font-size: 100%;
} /*16px*/

:root {
  /* colors */
  --primary-50: #f5ecfd;
  --primary-100: #ebdafa;
  --primary-200: #d6b5f5;
  --primary-300: #c28ff1;
  --primary-400: #ad6aec;
  --primary-500: #9945e7;
  --primary-600: #7a37b9;
  --primary-700: #5c298b;
  --primary-800: #3d1c5c;
  --primary-900: #1f0e2e;

  /* grey */
  --grey-50: #f8fafc;
  --grey-100: #f1f5f9;
  --grey-200: #e2e8f0;
  --grey-300: #cbd5e1;
  --grey-400: #94a3b8;
  --grey-500: #64748b;
  --grey-600: #475569;
  --grey-700: #334155;
  --grey-800: #1e293b;
  --grey-900: #0f172a;
  /* rest of the colors */
  --black: #222;
  --white: #fff;
  --red-light: #e58189;
  --red-dark: #842029;
  --green-light: #d1e7dd;
  --green-dark: #0f5132;

  /* fonts  */
  --bodyFont: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --small-text: 0.875rem;
  --extra-small-text: 0.7em;
  /* rest of the vars */
  --backgroundColor: var(--grey-100);
  --textColor: var(--grey-900);
  --borderRadius: 0.25rem;
  --letterSpacing: 1px;
  --transition: 0.3s ease-in-out all;
  --max-width: 1120px;
  --fixed-width: 600px;

  /* box shadow*/
  --shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-2: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-3: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --shadow-4: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);

  --shadow-primary: 0 4px 6px -1px rgba(153, 69, 231, 0.1),
    0 2px 4px -1px rgba(153, 69, 231, 0.06);
}

body {
  background: var(--white);
  font-family: var(--bodyFont);
  line-height: 1.5;
  color: var(--textColor);
  font-size: 0.875rem;
}
hr {
  border: none;
  border-top: 1px solid var(--grey-300);
  width: 100%;
}

p {
  margin-bottom: 1.25rem;
  color: var(--grey-700);
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.25rem;
  line-height: 1.3;
  text-transform: capitalize;
  letter-spacing: var(--letterSpacing);
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

h5 {
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}
a {
  color: var(--primary-500);
  text-decoration: none;
}

small,
.text-small {
  font-size: var(--small-text);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 2rem;
  }
  h4 {
    font-size: 1.5rem;
  }
  h5 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
    font-weight: 600;
  }
}

ul {
  list-style-type: none;
  padding: 0;
}

img {
  width: 100%;
  display: block;
  object-fit: cover;
}

/* global classes */

/* section */
.section {
  padding: 1rem 0;
}
.section-center {
  width: 94vw;
  margin: 0 auto;
  max-width: var(--max-width);
}

@media screen and (min-width: 992px) {
  .section {
    padding: 2rem 0;
  }
  .section-center {
    width: 95vw;
  }
}
.text-center {
  text-align: center;
}
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

/* buttons */
.icon-text-btn {
  display: flex;
  column-gap: 0.25rem;
  align-items: center;
}
.inside-icon {
  display: flex;
  font-size: 1rem;
  color: var(--primary-700);
}

.btn {
  cursor: pointer;
  color: var(--white);
  background: var(--primary-500);
  border: 2px solid transparent;
  border-radius: var(--borderRadius);
  letter-spacing: var(--letterSpacing);
  padding: 0.375rem 0.75rem;
  box-shadow: var(--shadow-1);
  transition: var(--transition);
  text-transform: capitalize;
  display: inline-block;
}
.btn:active {
  opacity: 0.8;
}

.btn-hipster {
  color: var(--primary-500);
  background: var(--primary-200);
}

.btn-block {
  width: 100%;
}
.btn-icon {
  background: transparent;
  border: transparent;
  padding: 0;
  display: flex;
  align-self: center;
  transition: var(--transition);
  color: var(--primary-500);
  cursor: pointer;
}

.btn-icon > svg {
  font-size: 1rem;
}

.btn-icon:active {
  transform: translateY(1px);
}
.text-btn {
  background: transparent;
  border: transparent;
  color: var(--primary-500);
  font-size: var(--small-text);
  text-transform: capitalize;
  cursor: pointer;
  padding: 2px 0;
  transition: var(--transition);
  user-select: none;
}
.text-btn:hover {
  text-decoration: underline;
}
@media screen and (min-width: 800px) {
  .btn-icon > svg {
    font-size: 1.35rem;
  }
  .text-btn {
    font-size: unset;
  }
}

/* discard button */

.discard-btn {
  background: transparent;
  color: var(--red-dark);
  border-color: var(--red-light);
}
.discard-btn:active {
  background: var(--red-light);
  color: var(--white);
}

/* save button */

.save-btn {
  background: transparent;
  color: var(--primary-500);
  border-color: var(--primary-500);
}

/* remove hovering from touch screen */
@media (hover: hover) and (pointer: fine) {
  .btn:hover {
    background: var(--primary-700);
    box-shadow: var(--shadow-3);
  }
  .save-btn:hover {
    background: var(--primary-500);
    color: var(--white);
  }
  .discard-btn:hover {
    background: var(--red-light);
    color: var(--white);
  }
  .btn-hipster:hover {
    color: var(--primary-200);
    background: var(--primary-700);
  }
  .btn-icon:hover {
    transform: scale(1.1);
  }
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: var(--borderRadius);
}

.alert-danger {
  color: var(--red-dark);
  background: var(--red-light);
}
.alert-success {
  color: var(--green-dark);
  background: var(--green-light);
}
/* form */

.form {
  width: 100%;
  background: var(--white);
  border-radius: var(--borderRadius);
  box-shadow: var(--shadow-2);
  padding: 1rem;
}
.form-label {
  display: block;
  font-size: var(--small-text);
  margin-bottom: 0.25rem;
  text-transform: capitalize;
  letter-spacing: var(--letterSpacing);
  white-space: nowrap;
}
.form-input,
.form-textarea {
  width: 100%;
  padding: 0.375rem 0.75rem;
  border-radius: var(--borderRadius);
  background: var(--backgroundColor);
  border: 1px solid var(--grey-200);
}

.form-row {
  margin-bottom: 1rem;
}

@media screen and (min-width: 800px) {
  .form {
    padding: 2rem;
  }
}

input:focus,
textarea:focus {
  outline-color: var(--primary-500);
  box-shadow: var(--shadow-primary);
  background: var(--white);
}

/* remove up down arrow */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

.form-textarea {
  resize: none;
}
::placeholder {
  font-family: inherit;
  color: var(--grey-400);
}
.form-alert {
  color: var(--red-dark);
  letter-spacing: var(--letterSpacing);
  text-transform: capitalize;
}
/* alert */

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

.loading {
  width: 6rem;
  height: 6rem;
  border: 5px solid var(--grey-400);
  border-radius: 50%;
  border-top-color: var(--primary-500);
  animation: spinner 0.6s linear infinite;
}
.loading {
  margin: 0 auto;
}
/* title */

.title {
  text-align: center;
}

.title-underline {
  background: var(--primary-500);
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

/* navbar total height= 64px footer height= 402px */
.page-100 {
  min-height: calc(100vh - 64px);
  padding: 5rem 0;
}
.page {
  min-height: calc(100vh - (20vh + 74px));
}

/* global animations */
@keyframes fade_in_show {
  0% {
    opacity: 0;
    transform: scale(0);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

*:focus,
a,
.btn,
div {
  -webkit-tap-highlight-color: transparent;
  /* outline: none; */
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}
